<template>
    <div class="barCharts">
        <div id="barCharts" ref="barCharts"></div>
    </div>
</template>

<script>
  import * as echarts from 'echarts';
  //这是绘制图表的参数定义
  let optionTemp = {
    tooltip: {
      trigger: 'axis',
      axisPointer: {
        type: 'cross',
        crossStyle: {
          color: '#999'
        }
      }
    },
    grid: {
      left: '2%',
      top: '42',
      right: '4%',
      bottom: '4%',
      containLabel: true
    },
    legend: {
      x:'center',      //可设定图例在左、右、居中
      y:'top',     //可设定图例在上、下、居中
      padding:[10,50,0,0],   //可设定图例[距上方距离，距右方距离，距下方距离，距左方距离
      textStyle: {color: '#fff'},
    },
    xAxis: [
      {
        type: 'category',
        data: ['安全生产', '市场营销', '人力资源', '财务管理', '物资管理', '信息管理', '综合管理'],
        axisPointer: {
          type: 'shadow'
        },
        axisTick: { //x轴刻度线
          show: false,
        },
        axisLine: { //x轴
          show: false,
          // lineStyle:{
          //   color:'#00FF00',
          // }
        },
        axisLabel: { //设置x轴的字
          show: true,
          interval:0,//使x轴横坐标全部显示
          // rotate:20,//旋转
          formatter:function(v){
            if (v.length>4){
              return v.slice(0,2)+'\n'+v.slice(2,4) + '..'
            }else {
              return v.slice(0,2)+'\n'+v.slice(2,v.length)
            }
          },
          textStyle: {//x轴字体样式
            color: "#fff",
            margin: 15,
            fontSize:12
            // fontWeight: 'bold',
          }
        },
      }
    ],
    yAxis: [
      {
        type: 'value',
        name: '单位：个',
        // min: 0,
        // max: 250,
        // interval: 50,
        nameTextStyle: {
          color: "#66B9FF",
        },
        axisLabel: { //设置y轴的字
          // show: true,
          // formatter: '{value} ml',
          color: '#fff',
          interval: 0,//使y轴横坐标全部显示
          textStyle: {//y轴字体样式
            color: "#66B9FF",
            fontSize: 12,
            margin: 20,
          }
        },
        splitLine: {//网格线样式
          show: true,
          lineStyle: {
            color: '#0065B3',
            width: 1,
            type: 'solid'
          }
        },
        axisTick: { //y轴刻度线
          show: false,
        },
        axisLine: { //y轴
          show: false,
        },
      },
      {
        type: 'value',
        name: '覆盖率%',
        min: 0,
        max: 100,
        interval: 20,
        nameTextStyle: {
          color: "#66B9FF",
        },
        axisLabel: { //设置y轴的字
          // show: true,
          formatter: '{value} %',
          color: '#fff',
          interval: 0,//使y轴横坐标全部显示
          textStyle: {//y轴字体样式
            color: "#66B9FF",
            fontSize: 12,
            margin: 20,
          }
        },
        splitLine: {//网格线样式
          show: true,
          lineStyle: {
            color: '#0065B3',
            width: 1,
            type: 'solid'
          }
        },
        axisTick: { //y轴刻度线
          show: false,
        },
        axisLine: { //y轴
          show: false,
        },
      }
    ],
    series: []
  }


  export default {
    name: "barCharts",
    props: {
      xData:{
        type:Array,
        default:()=>{
          return []
        }
      },
      seriesData:{
        type:Array,
        default:()=>{
          return []
        }
      }
    },
    watch: {

    },
    data() {
      return {
        option: optionTemp,
        myChart:null,
      }


    },
    mounted() {
      this.init();
    },
    methods: {

      init() {
        if (this.seriesData.length>0){
          this.option.series = this.seriesData
        }
        if (this.xData.length>0) {
          this.option.xAxis[0].data = this.xData
        }
        let mychart = echarts.init(this.$refs.barCharts, );
        //将会绘制图表的参数加到图表中

        mychart.setOption(this.option,true);
        let $this = this;
        mychart.on('click', function (params) {
          $this.$emit('getData',params,$this.index)
          // window.console.log(params);
        });
        window.addEventListener("resize", ()=>{
          mychart.resize()
        });

      },
    },
    beforeDestroy() {
      /* 页面组件销毁的时候，别忘了移除绑定的监听resize事件，否则的话，多渲染几次
      容易导致内存泄漏和额外CPU或GPU占用哦*/
      window.removeEventListener("resize", () => {
        this.myChart.resize();
      });
    },

  }
</script>

<style scoped lang="scss">
    .barCharts {
        height: 100%;
        #barCharts {
            height: 100%;
            /*width: 97%;*/
            /*height: 99%;*/
        }
    }
</style>